<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Employees</h4></li>
            <li class="collection-item" v-for="employee in employees " :key="employee.id">
                <div class="chip">{{employee.dept}}</div>
                {{employee.employee_id}}  /  {{employee.name}}
                <router-link class="right" :to="{name: 'ViewEmployee', params: {employee_id : employee.employee_id}}" >
                    <i class="fa fa-eye"></i>
                </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red" >
                <i class="fa fa-plus float-right"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from '@/firebase/init'
export default {
    name:'Dashboard',
    data(){
        return{
            employees: []
        }
    },
    created() {
        db.collection('employees').orderBy('dept').get()
        .then(snapshot => {
            snapshot.forEach(doc => {
                //console.log(doc.data())
                const data = {
                    id: doc.id,
                    employee_id: doc.data().employee_id,
                    name: doc.data().name,
                    dept: doc.data().dept,
                    position: doc.data().position
                }
                this.employees.push(data)
            });
        })
    }
}
</script>

<style>
    
</style>


